.sign-in {
  width: 380px;
  display: flex;
  flex-direction: column;

  .title {
    margin: 10px 0;
  }

  .buttons {
    display: flex;
    justify-content: space-between;
  }
}

@media screen and (max-width:992px){
    .sign-in {
        width:100%;
        margin-bottom: 30px;
    }
}
